@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
    margin: 0;
    /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: blanchedalmond;
    font-family: 'Roboto', sans-serif;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.body {
    background-color: rgba(0, 0, 0);
    font-family: 'Roboto', sans-serif;
}

body {
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
}

.btn {
    border-radius: 0 !important;
    cursor: pointer;
}

.clicker {
    height: 300px;
    width: 300px;
    margin: 0 auto;
    overflow: hidden;
}

.clicker-display {
    height: 200px;
}

.clicker-button-panel {
    height: 100px;
}

.header {
    height: 100px;
}
.App {
    color: blanchedalmond;
}

.not-found {
    text-align: center;
    font-size: 50px;
    font-weight: bold;
}

.image-search {
    margin-top: 100px;
}
.image-search-title-container {
    margin: 20px;
}
.image-search-title {
    text-align: center;
    font-weight: 10px;
    font-size: 80px;
    text-shadow: 2px 0 0 blanchedalmond, 4px 0 0 blanchedalmond, 8px 0 0 rgb(20, 40, 95), 0 8px 0 rgb(209, 178, 92), 12px 0 0 rgb(20, 40, 95),
        8px 10px 0 rgb(209, 178, 92);
}

.image-search-form {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 50px;
    color: red;
}
.Image-input {
    background-color: transparent;
    border: none;
    box-shadow: none;
    outline: none;
    border-bottom: rgb(18, 36, 87) solid 2px;
    color: blanchedalmond;
    font-weight: bold;
}
.Image-input:focus {
    color: blanchedalmond;
}
.btn-personal-style {
    margin-left: 10px;
    font-weight: bold;
    color: blanchedalmond;
    border: rgb(18, 36, 87) solid 1px;
    background-color: black;
    border-radius: 2px;
    outline: none;
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
}
.btn-personal-style:hover {
    background-color: rgb(29, 29, 29);
    border: rgb(30, 61, 146) solid 1px;

    -webkit-transition: ease 0.5s;

    transition: ease 0.5s;
}

.image-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.image-item {
    min-width: 400px;
    width: 30%;
    height: 25%;
    margin: 20px;
}

img {
    width: 100%;
    height: 250px;
}
.image-item button {
    width: 100%;
    border: none;
    color: blanchedalmond;
    background-color: rgb(18, 36, 87);
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
}
.image-item button:hover {
    background-color: rgb(30, 61, 146);
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
}

.image-container {
    color: blanchedalmond;
    margin: 30px;
}
.image-container img {
    width: 500px;
    height: auto;
}
.image-container button {
    border: none;
    color: blanchedalmond;
    background-color: rgb(18, 36, 87);
    padding: 5px 10px;
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
}
.image-container button:hover {
    background-color: rgb(30, 61, 146);
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
}

